export default {
  props: ['banners', 'date'],
  data() {
    return {

    }
  },
  computed: {
    updateDate() {
      if (this.date) {
        let formatDate = new Date(this.date)
        return formatDate.getMonth()+1 + '月' + formatDate.getDate() + '日更新'
      }
    }
  },
  methods: {
    handleClick(item) {
      this.$emit('swiperClick', item)
    }
  }
}
